import './ToastMessage.css';
import * as Toast from '@radix-ui/react-toast';
import {Text} from '@radix-ui/themes';
import React from 'react';

function ToastMessage(
    {open, type, message, onClose}: ToastMessageData & { onClose: () => void }) {

    return (
        <Toast.Provider swipeDirection='up'>
            <Toast.Root className='ToastRoot' open={open}>
                <Toast.Title className='ToastTitle'>{type === 'error' ? 'Erro' : 'Sucesso'}</Toast.Title>
                <Toast.Description className='ToastDescription' asChild>
                    <Text>{message}</Text>
                </Toast.Description>
                <Toast.Action className={`ToastAction Button small ${type === 'error' ? 'red' : 'green'}`}
                              asChild altText='mensagem'>
                    <button onClick={() => onClose()}>Ok
                    </button>
                </Toast.Action>
            </Toast.Root>
            <Toast.Viewport className='ToastViewport'/>
        </Toast.Provider>
    );
}

export type ToastMessageData = {
    open?: boolean;
    type?: 'error' | 'success';
    message?: string;
}

export default ToastMessage;