import React, {useState} from 'react';
import {useLocation, useNavigate} from 'react-router-dom';
import {Box, Button, Card, Container, Dialog, Flex, Heading, Link, Separator, Text, TextField} from '@radix-ui/themes';
import ToastHandler from "../components/ToastMessageHandler";

import ToastMessage from "../components/ToastMessage";

function RideInProgress() {
    const {state: ride} = useLocation();
    const navigate = useNavigate();
    const toastHandler = ToastHandler();
    const toastMessageData = toastHandler.data;

    const finishRide = async (price: number) => {
        const response = await fetch('http://localhost:8080/rides/finish-ride', {
            method: 'POST',
            headers: {
                'Content-Type': 'application/json',
            },
            body: JSON.stringify({
                id: ride.id,
                price: price
            })
        })
        const body = await response.json();
        if (response.status !== 200) {
            toastHandler.error(body.message);
        } else {
            toastHandler.success('Corrida finalizada com sucesso');
            setTimeout(() => navigate('/driver'), 1000);
        }
    };

    return (
        <>
            <ToastMessage open={toastMessageData.open}
                          message={toastMessageData.message}
                          type={toastMessageData.type}
                          onClose={toastHandler.onClose}/>
            <Flex justify='center' pt="8">
                <Box maxWidth='780px' minWidth='480px'>
                    <Card>
                        <Container pt="6" pl="6" pr="6">
                            <Heading as='h3' size='6' trim='start' mb='5'>
                                Corrida{' '}
                                <Link href='#' weight='bold' onClick={(e) => e.preventDefault()}>
                                    #{ride.id}
                                </Link>
                            </Heading>
                            <Box>
                                <Text as='div' size='2' mb='1' color='gray'>
                                    Partida
                                </Text>
                                <Text as='div' size='3' weight='bold'>
                                    {ride.pick_up}
                                </Text>
                            </Box>
                            <Box>
                                <Text as='div' size='2' mb='1' color='gray'>
                                    Destino
                                </Text>
                                <Text as='div' size='3' weight='bold'>
                                    {ride.drop_off}
                                </Text>
                            </Box>
                            <Box>
                                <Text size='2' mb='1' color='gray'>
                                    Passageiro
                                </Text>
                            </Box>
                            <Text size='3' mb='1' weight='bold'>
                                {ride.passenger.name}
                            </Text>
                            <Box mb="3">
                                <Separator size='4' mt='1'/>
                            </Box>
                            <Box>
                                <Flex mt="3" mb="1" justify="end" gap="3">
                                    <ConfirmationDialog onConfirm={finishRide}/>
                                </Flex>
                            </Box>
                        </Container>
                    </Card>
                </Box>
            </Flex>
        </>
    );
}

export default RideInProgress;

function ConfirmationDialog({onConfirm}: Readonly<{ onConfirm: Function }>) {
    const [price, setPrice] = useState<number>(0.00);
    return (
        <Dialog.Root>
            <Dialog.Trigger>
                <Button color="green">
                    Finalizar corrida
                </Button>
            </Dialog.Trigger>

            <Dialog.Content maxWidth="450px">
                <Dialog.Title>Confirmação</Dialog.Title>
                <Dialog.Description size="2" mb="4">
                    Informe o valor e confirme o término da corrida
                </Dialog.Description>

                <Flex direction="column" gap="3">
                    <label>
                        <Text as="div" size="2" mb="1" weight="bold">
                            Valor
                        </Text>
                        <TextField.Root
                            type="number"
                            placeholder="Informe o valor em R$"
                            onChange={(e) => setPrice(e.target.valueAsNumber)}
                        />
                    </label>
                </Flex>
                <Flex gap="3" mt="4" justify="end">
                    <Dialog.Close>
                        <Button variant="soft" color="gray">
                            Cancelar
                        </Button>
                    </Dialog.Close>
                    <Dialog.Close>
                        <Button disabled={!price} onClick={() => onConfirm(price)}>Confirmar</Button>
                    </Dialog.Close>
                </Flex>
            </Dialog.Content>
        </Dialog.Root>
    )
}