import {Button, Callout, Container, Grid, Link, Strong, Text, TextField} from '@radix-ui/themes';
import {DrawingPinIcon, EnvelopeClosedIcon, InfoCircledIcon, PersonIcon} from '@radix-ui/react-icons'
import Banner from '../components/Banner';
import * as Form from '@radix-ui/react-form';
import './Home.css';
import React from 'react';
import ToastMessage from '../components/ToastMessage';
import ToastHandler from '../components/ToastMessageHandler';
import {useNavigate} from 'react-router-dom';

function Home() {
    const navigate = useNavigate();
    return (
        <>
            <Callout.Root>
                <Callout.Icon>
                    <InfoCircledIcon/>
                </Callout.Icon>
                <Callout.Text>
                    Você é motorista?
                    <Link href='#' onClick={() => navigate('/driver')} color='green' underline='always'> Clique
                        aqui</Link>
                </Callout.Text>
            </Callout.Root>
            <Banner></Banner>
            <Container mt='8' mb='4' style={{textAlign: 'center'}}>
                <Text weight='light' align='center' size='5'>Seja bem-vindo ao <Strong>Ridely</Strong>, preencha
                    seus <br/>
                    dados
                    abaixo para iniciar uma
                    corrida</Text>
            </Container>
            <Container maxWidth='400px'>
                <Grid justify='center' mt='4' columns='1' pr='2' pl="2" width="auto">
                    <RideForm/>
                </Grid>
            </Container>
        </>
    );
}

export default Home;

function RideForm() {
    const navigate = useNavigate();
    const toastHandler = ToastHandler();
    const toastMessageData = toastHandler.data;
    const requestRide = async (event: any) => {
        event.preventDefault();
        const data = Object.fromEntries(new FormData(event.currentTarget)) as Record<string, string>;
        const response = await fetch('http://localhost:8080/rides/request-driver', {
            method: 'POST',
            headers: {
                'Content-Type': 'application/json',
            },
            body: JSON.stringify({
                passenger: {
                    name: data?.nome,
                    email: data?.email,
                },
                pick_up: data?.partida,
                drop_off: data?.destino
            })
        })
        const body = await response.json();
        if (response.status !== 200) {
            toastHandler.error(body.message);
        } else {
            toastHandler.success('Corrida iniciada, aguarde o motorista');
            navigate('/ride-requested', {state: body});
        }
    }
    return (
        <>
            <ToastMessage open={toastMessageData.open}
                          message={toastMessageData.message}
                          type={toastMessageData.type}
                          onClose={toastHandler.onClose}/>
            <Form.Root onSubmit={requestRide}>
                <Form.Field className='FormField' name='nome'>
                    <Form.Message match='valueMissing' asChild>
                        <Text size='1'>Por favor, informe seu nome</Text>
                    </Form.Message>
                    <Form.Control asChild>
                        <TextField.Root required placeholder='Seu nome' mb='2'>
                            <TextField.Slot>
                                <PersonIcon height='16' width='16'></PersonIcon>
                            </TextField.Slot>
                        </TextField.Root>
                    </Form.Control>
                </Form.Field>
                <Form.Field className='FormField' name='email'>
                    <Form.Message match='valueMissing' asChild>
                        <Text size='1'>Por favor, informe seu email</Text>
                    </Form.Message>
                    <Form.Control asChild>
                        <TextField.Root required placeholder='Seu email' mb='2'>
                            <TextField.Slot>
                                <EnvelopeClosedIcon height='16' width='16'/>
                            </TextField.Slot>
                        </TextField.Root>
                    </Form.Control>
                </Form.Field>
                <Form.Field className='FormField' name='partida'>
                    <Form.Message match='valueMissing' asChild>
                        <Text size='1'>Por favor, informe o endereço de partida</Text>
                    </Form.Message>
                    <Form.Control asChild>
                        <TextField.Root required placeholder='Endereço de partida'
                                        mb='2'>
                            <TextField.Slot>
                                <DrawingPinIcon height='16' width='16'/>
                            </TextField.Slot>
                        </TextField.Root>
                    </Form.Control>
                </Form.Field>
                <Form.Field className='FormField' name='destino'>
                    <Form.Message match='valueMissing' asChild>
                        <Text size='1'>Por favor, informe o endereço de destino</Text>
                    </Form.Message>
                    <Form.Control asChild>
                        <TextField.Root required placeholder='Endereço de destino'
                                        mb='2'>
                            <TextField.Slot>
                                <DrawingPinIcon height='16' width='16'/>
                            </TextField.Slot>
                        </TextField.Root>
                    </Form.Control>
                </Form.Field>
                <Container style={{width: '100%', display: 'flex'}} mt='2'>
                    <Form.Submit asChild>
                        <Button style={{float: 'right'}}>Solicitar Corrida</Button>
                    </Form.Submit>
                </Container>
            </Form.Root>
        </>
    );
}