import React, {useState} from 'react';
import {useLocation, useNavigate} from 'react-router-dom';
import {Box, Button, Card, Flex, Heading, Link, Separator, Strong, Text} from '@radix-ui/themes';
import ToastMessage from "../components/ToastMessage";
import ToastHandler from "../components/ToastMessageHandler";
import {UpdateIcon} from "@radix-ui/react-icons";
import {Ride} from "../types/ride.type";

function RideRequested() {
    const {state} = useLocation();
    const [ride, setRide] = useState(state);
    const navigate = useNavigate();
    const toastHandler = ToastHandler();
    const toastMessageData = toastHandler.data;

    const cancel = async () => {
        const response = await fetch('http://localhost:8080/rides/cancel-ride', {
            method: 'POST',
            headers: {
                'Content-Type': 'application/json',
            },
            body: JSON.stringify({
                id: ride.id
            })
        })
        const body = await response.json();
        if (response.status !== 200) {
            toastHandler.error(body.message);
        } else {
            toastHandler.success('Corrida cancelada com sucesso');
            setTimeout(() => navigate('/'), 1000);
        }
    }

    const back = (): void => {
        navigate(-1);
    }

    const refresh = async () => {
        const response = await fetch(`http://localhost:8080/rides/${ride.id}`, {
            method: 'GET',
            headers: {
                'Content-Type': 'application/json',
            },
        })
        const body = await response.json();
        if (response.status !== 200) {
            toastHandler.error(body.message);
        } else {
            setRide(body)
        }
    }

    return (
        <>
            <ToastMessage open={toastMessageData.open}
                          message={toastMessageData.message}
                          type={toastMessageData.type}
                          onClose={toastHandler.onClose}/>
            <Flex justify='center' pt="8">
                <Box maxWidth='780px' minWidth='480px'>
                    <Card size='4'>
                        <Header ride={ride}/>
                        <RideInfo ride={ride}/>
                        <Footer onCancel={cancel} onBack={back} onRefresh={refresh}/>
                    </Card>
                </Box>
            </Flex>
        </>
    );
}

export default RideRequested;

function Header({ride}: Readonly<{ ride: Ride }>) {
    return (
        <Heading as='h3' size='6' trim='start' mb='5'>
            Corrida{' '}
            <Link href='#' weight='bold' onClick={(e) => e.preventDefault()}>
                #{ride.id}
            </Link>
        </Heading>
    );
}

function RideInfo({ride}: Readonly<{ ride: Ride }>) {
    const convertStatus = (status: string): string => {
        switch (status) {
            case 'REQUESTED':
                return 'Iniciada, aguarde a confirmação do motorista';
            case 'COMPLETED':
                return 'Finalizada';
            case 'IN_PROGRESS':
                return 'O motorista confirmou a corrida e está indo até você';
            case 'CANCELLED':
                return 'Cancelada';
            case 'REFUSED':
                return 'Recusada';
        }
        return 'Não Definido';
    }

    return (
        <>
            <Box>
                <Text as='div' size='2' mb='1' color='gray'>
                    Partida
                </Text>
                <Text as='div' size='3' weight='bold'>
                    {ride.pick_up}
                </Text>
            </Box>

            <Box>
                <Text as='div' size='2' mb='1' color='gray'>
                    Destino
                </Text>
                <Text as='div' size='3' weight='bold'>
                    {ride.drop_off}
                </Text>
            </Box>

            <Box>
                <Text as='div' size='2' mb='1' color='gray'>
                    Situação
                </Text>
                <Text as='div' size='3' mb='1' weight='bold'>
                    {convertStatus(ride.status)}
                </Text>
            </Box>

            <Flex direction='column' gap='1' gridColumn='1'>
                <Separator size='4' mt='1' mb='2'/>
                <Flex justify='between'>
                    <Text size='2' mb='1' color='gray'>
                        Motorista
                    </Text>
                    <Text size='2' mb='1' color='gray'>
                        Veículo
                    </Text>
                </Flex>
                <Flex justify='between'>
                    <Text size='3' mb='1' weight='bold'>
                        {ride.driver.name}
                    </Text>
                    <Text
                        size='2'>{ride.driver.car.model} - {ride.driver.car.color} [<Strong>{ride.driver.car.license_plate}</Strong>]</Text>
                </Flex>
                <Box>
                    <Separator size='4' mt='1' mb='1'/>
                </Box>
            </Flex>
        </>
    );
}

function Footer({onCancel, onBack, onRefresh}: Readonly<{
    onCancel: Function,
    onBack: Function,
    onRefresh: Function
}>) {
    return (
        <Flex mt='6' justify='end' gap='3'>
            <Button variant='outline' color='red' onClick={() => onCancel()}>
                Cancelar
            </Button>
            <Button variant="outline" color="gray" onClick={() => onBack()}>Voltar</Button>
            <Button color="blue" onClick={() => onRefresh()}>
                Atualizar
                <UpdateIcon height="16" width="16"/>
            </Button>
        </Flex>
    );
}