import React, {useState} from 'react';
import {
    Box,
    Button,
    Card,
    Container,
    Flex,
    Heading,
    IconButton,
    Link,
    Separator,
    Text,
    TextField
} from '@radix-ui/themes';
import {MagnifyingGlassIcon, UpdateIcon} from '@radix-ui/react-icons';
import ToastHandler from '../components/ToastMessageHandler';
import ToastMessage from '../components/ToastMessage';
import {useNavigate} from 'react-router-dom';
import {Ride} from "../types/ride.type";

function Driver() {
    const [driverId, setDriverId] = useState<number | undefined>(undefined);
    const [openRide, setOpenRide] = useState<Ride | null>(null);
    const navigate = useNavigate();
    const toastHandler = ToastHandler();
    const toastMessageData = toastHandler.data;

    const checkRide = async (driverId: number): Promise<void> => {
        setDriverId(driverId);
        const response = await fetch(`http://localhost:8080/drivers/${driverId}/get-rides`, {
            method: 'GET',
            headers: {
                'Content-Type': 'application/json',
            },
        })
        const body = await response.json();
        if (response.status !== 200) {
            toastHandler.error(body.message);
            setOpenRide(null);
        } else {
            setOpenRide(body);
        }
    }

    const acceptRide = async () => {
        const response = await fetch(`http://localhost:8080/rides/accept-ride`, {
            method: 'POST',
            headers: {
                'Content-Type': 'application/json',
            },
            body: JSON.stringify({
                id: openRide?.id
            })
        })
        const body = await response.json();
        if (response.status !== 200) {
            toastHandler.error(body.message);
        } else {
            toastHandler.success('Corrida iniciada, já avisamos o passageiro(a) que você está indo até ele(a)');
            navigate('/ride-in-progress', {state: body})
        }
    }

    const refuseRide = async () => {
        const response = await fetch(`http://localhost:8080/rides/refuse-ride`, {
            method: 'POST',
            headers: {
                'Content-Type': 'application/json',
            },
            body: JSON.stringify({
                id: openRide?.id
            })
        })
        const body = await response.json();
        if (response.status !== 200) {
            toastHandler.error(body.message);
        } else {
            await checkRide(driverId!);
        }
    }

    const refresh = async () => {
        if (driverId) {
            await checkRide(driverId);
        }
    }

    return (
        <>
            <ToastMessage open={toastMessageData.open}
                          message={toastMessageData.message}
                          type={toastMessageData.type}
                          onClose={toastHandler.onClose}/>
            <Container p='9'>
                <Header/>
                <IdentificationForm onConfirm={checkRide}/>
                {
                    !openRide ? <NoRideContent/> :
                        <RideContent openRide={openRide} onAccept={acceptRide} onRefuse={refuseRide}/>
                }
                <Footer onRefresh={refresh}/>
            </Container>
        </>
    );
}

export default Driver;

function Header() {
    return (
        <Flex justify='center'>
            <Heading as='h3' size={{
                initial: '4',
                md: '7',
            }}>
                Informe sua identificação para monitorar sua próxima corrida
            </Heading>
        </Flex>
    );
}

function IdentificationForm({onConfirm}: Readonly<{ onConfirm: Function }>) {
    const [driverId, setDriverId] = useState<string | undefined>(undefined);
    return (
        <Flex justify='center'>
            <Box maxWidth='350px' mt='6'>
                <TextField.Root required placeholder='Identificação' mb='2' size='3'
                                onBlur={() => {
                                    if (driverId) onConfirm(driverId);
                                }}
                                onChange={(e) => setDriverId(e.target.value)} type='number'>
                    <TextField.Slot>
                    </TextField.Slot>
                    <TextField.Slot pr='3'>
                        <IconButton size='2' variant='ghost' onClick={() => onConfirm(driverId)}>
                            <MagnifyingGlassIcon height='20' width='20'/>
                        </IconButton>
                    </TextField.Slot>
                </TextField.Root>
            </Box>
        </Flex>
    );
}

function NoRideContent() {
    return (
        <Box mt='6'>
            <Flex justify='center'>
                <img src='/stop-car.png' style={{
                    opacity: '30%',
                    borderRadius: '20px',
                    height: 'auto',
                    maxWidth: '20vh',
                    width: 'auto',
                    display: 'flex',
                }} alt='Stop Car'/>

            </Flex>
            <Flex justify='center' mt='4'>
                <Text weight='light'>Nenhuma corrida aguardando, clique em 'Atualizar' para verificar
                    novamente...</Text>
            </Flex>
        </Box>
    );
}

function RideContent({openRide, onAccept, onRefuse}: Readonly<{
    openRide: Ride,
    onAccept: Function,
    onRefuse: Function
}>) {
    return (
        <Flex justify='center' mt='6'>
            <Box maxWidth='780px' minWidth='480px'>
                <Card>
                    <Box p='3'>
                        <Heading as='h3' size='6' trim='start' mb='5'>
                            Nova corrida: {' '}
                            <Link href='#' weight='bold' onClick={(e) => e.preventDefault()}>
                                #{openRide.id}
                            </Link>
                        </Heading>

                        <Container>
                            <Box>
                                <Text as='div' size='2' mb='1' color="gray">
                                    Partida
                                </Text>
                                <Text as="div" size="3" weight="bold">
                                    {openRide.pick_up}
                                </Text>
                            </Box>

                            <Box>
                                <Text as="div" size="2" mb="1" color="gray">
                                    Destino
                                </Text>
                                <Text as="div" size="3" weight="bold">
                                    {openRide.drop_off}
                                </Text>
                            </Box>

                            <Separator size="4" mt="5" mb="2"/>
                            <Box>
                                <Text as="div" size="2" mb="1" color="gray">
                                    Passageiro
                                </Text>
                                <Text as="div" size="3" mb="1" weight="bold">
                                    {openRide.passenger?.name}
                                </Text>
                            </Box>
                            <Separator size="4" mt="1" mb="2"/>

                            <Flex justify="end" pt="3">
                                <Button color="red" onClick={() => onRefuse()}>
                                    Recusar
                                </Button>
                                <Button color="green" ml="2" onClick={() => onAccept()}>Aceitar</Button>
                            </Flex>
                        </Container>
                    </Box>
                </Card>
            </Box>
        </Flex>
    )
}

function Footer({onRefresh}: Readonly<{ onRefresh: Function }>) {
    const navigate = useNavigate();
    const back = (): void => {
        navigate('/');
    }
    return (
        <Flex justify="end" mt="7">
            <Button variant="outline" color="gray" onClick={() => back()}>Voltar</Button>
            <Button color="green" ml="3" onClick={() => onRefresh()}>
                Atualizar
                <UpdateIcon height="16" width="16"/>
            </Button>
        </Flex>
    );
}