import React from "react";
import {ToastMessageData} from "./ToastMessage";

function ToastHandler() {
    const [toastMessageData, setToastMessageData] = React.useState <ToastMessageData>({open: false});

    const error = (message: string): void => {
        setToastMessageData({
            open: true,
            type: 'error',
            message: message
        });
    }

    const success = (message: string): void => {
        setToastMessageData({
            open: true,
            type: 'success',
            message: message
        });
    }

    const onClose = (): void => {
        setToastMessageData({
            ...toastMessageData,
            open: false
        })
    }

    return {
        error,
        success,
        onClose,
        data: toastMessageData
    }
}

export default ToastHandler;